@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
.library{position:fixed;width:30%;top:0px;left:0px;height:100%;background-color:white;overflow-y:scroll;box-shadow:2px 2px 50px #a3a3a3;-webkit-transform:translateX(-100%);transform:translateX(-100%);opacity:0;transition:all 1s ease-out}.library h1{padding:2rem}.library-song-container{display:flex;padding:1rem 2rem;transition:background 0.5s ease-out}.library-song-container img{width:30%}.library-song-container:hover{background-color:#cac7e7;cursor:pointer}.library-song-container .song-detail{padding:1rem}*{scrollbar-width:thin;scrollbar-color:#949090 transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px #949090;border-radius:10px}*::-webkit-scrollbar-thumb{border-radius:10px;-webkit-box-shadow:inset 0 0 6px #949090}.selected{background-color:#dfc7c2}.library-active{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%)}@media screen and (max-width: 768px){.library{width:100%}}

*{margin:0px;padding:0px;box-sizing:border-box}h1,h2,h3{color:#444}body{font-family:"Lato", sans-serif}h3,h4{font-weight:400;color:#6e6e6e}.app{transition:all 0.5 ease}.app-active{margin-left:30%}.player{height:20vh;display:flex;align-items:center;justify-content:center;flex-direction:column}.player .time-control{width:50%;display:flex;align-items:center;justify-content:space-between}.player .time-control input{padding:1rem 0rem;width:100%;-webkit-appearance:none;width:100%;background:transparent}.player .time-control p{padding:1rem;font-weight:bold}.player .play-control{width:30%;display:flex;align-items:center;justify-content:space-between;padding:1rem}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px;background-color:black}.track{background-color:lightcoral;width:100%;position:relative;height:1rem;overflow:hidden}.animate-track{height:100%;width:100%;background-color:gray;position:absolute;top:0;left:0;-webkit-transform:translateX(10%);transform:translateX(10%);pointer-events:none}@media screen and (max-width: 768px){.player .time-control{width:80%}.player .play-control{width:80%}}.song-container{height:44vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.song-container img{width:20%;border-radius:50%}.song-container h1{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@media screen and (max-width: 768px){.library-container img{width:36% !important}}.nav{height:20vh;display:flex;align-items:center;justify-content:space-around}.library-button{padding:0.9rem;border:none;background-color:white;cursor:pointer;transition:all 1s ease-in-out;border:1px solid gray}.library-button:hover{background:gray;color:white}@media screen and (max-width: 768px){.library-button{z-index:100}}

